.AddForm{
    background: rgba( 255, 255, 255, 0.1 );
box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
backdrop-filter: blur( 3px );
-webkit-backdrop-filter: blur( 3px );
border-radius: 10px;
border: 1px solid rgba( 255, 255, 255, 0.18 );
    width: 400px;
    height: 400px;
    padding: 20px;
}
.AddTodo{
    background: url("https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSinKLCbeURS2IkA5p-xsFMqqFBk3yJKeu2GA&usqp=CAU");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}
.title{
    padding-left: 40px;
    padding-right: 40px;
}
.title input{
    width: 100%;
    height: 30px;
    margin-top: 15px;
    background: rgba( 255, 255, 255, 0.1 );
    border-radius: 10px;
border: 1px solid rgba( 255, 255, 255, 0.18 );
backdrop-filter: blur( 3px );
-webkit-backdrop-filter: blur( 3px );
}
.title h4{
    margin-top: 15px;
}
.date{
    padding: 0 40px 0 40px;
}
.date input{
    width: 100%;
    height: 30px;
    margin-top: 15px;
    background: rgba( 255, 255, 255, 0.1 );
    border-radius: 10px;
border: 1px solid rgba( 255, 255, 255, 0.18 );
backdrop-filter: blur( 3px );
-webkit-backdrop-filter: blur( 3px );
}
.date h4{
    margin-top: 15px;
}
.des{
    padding: 0 40px 0 40px;
}
.des textarea{
    margin-top: 15px;
    background: rgba( 255, 255, 255, 0.1 );
    border-radius: 10px;
border: 1px solid rgba( 255, 255, 255, 0.18 );
backdrop-filter: blur( 3px );
-webkit-backdrop-filter: blur( 3px );
}
.des h4{
    margin-top: 15px;
}
button{
    text-align: center;
    margin-left: 125px;
    padding: 15px;
    width: 100px;
    height: 50px;
    margin-top: 15px;
}