@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,400;0,600;0,700;1,400;1,500&family=Tilt+Prism&display=swap');
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
.home{
    width: 100%;
    height: 80vh;
    background-color: white;
    color: black;
    /* margin-bottom: 50px; */
}
.Header{
    display: flex;
    justify-content: space-between;
    padding: 30px;
    /* padding-bottom: 35px; */
    margin-top: 20px;
}
.Header button{
    width: 200px;
    height: 50px;
    background-color: tomato;
    color: white;
    border: tomato;
    transition: 0.5s;
}
.logo{
    font-size: 25px;
    font-weight: 700;
    color: tomato;
    /* font-family: 'Tilt Prism', cursive; */
    letter-spacing: 3px;
}
.adam{
    display: flex;
    justify-content: space-around;
    margin-top: 50px;
}
.content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
}
.img{
    width: 400px;
    height: 400px;
}
.img img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.content p{
    margin-top: 20px;
    font-size: 20px;
}
span{
    color: tomato;
}
.typewriter{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.typewriter span{
    margin-top: 10px;
    font-weight: 700;
}

.About{
    text-align: center;
     font-weight: 500;
     padding-bottom: 50px;
     margin-top: 150px;
}
.About .name{
    color: tomato;
    padding-bottom: 50px;
}
.container{
    height: 100%;
}
.about-content{
    display: flex;
    justify-content: space-around;
    width: 100%;
}
.img-about img{
    width: 200px;
    height: 200px;
    margin-left: 100px;
}
.story{
    margin-right: 200px;
    padding-top: 35px;
}
.img-about{
    display: flex;
    justify-content: center;
    align-items: center;
}
.about h2{
    margin-bottom: 25px;
    margin-left: 100px;
}
.skills{
    width: 100%;
}
.topic-skills{
    width: 100%;
    text-align: center;
    margin-top: 100px;
    color: tomato;
    font-weight: 500;
}
.language-container{
    padding: 20px;
    overflow: hidden;
    margin-top: 100px;
    margin-bottom: 100px;
}
.speak{
    width: 100%;
}
.speak-content{
    text-align: center;
}
.form input{
    width: 500px;
    height: 50px;
    padding: 20px;
    border: 2px solid black;
}
input:focus{
    outline: none;
    /* color: tomato; */
}
.form button{
    width: 200px;
    height: 50px;
    background-color: tomato;
    color: white;
    border: 2px solid tomato;
    margin-left: 20px;
    transition: 0.5s;
}
.form{
    margin-top: 70px;
    margin-bottom: 100px;
}
.speak{
    width: 100%;
}

.slide{
    margin-left: 70px;
}

.typewriter{
    font-weight: 500;
    font-size: 18px;
}

.btn button{
    width: 100px;
    height: 40px;
    background-color: tomato;
    border: tomato;
    color: white;
    margin-right: 10px;
    margin-top: 25px;
    transition: 0.5s;
}
/* .link{
    width: 100px;
    height: 40px;
    color: black;
    background-color: tomato;
    padding: 10px;
} */
button:hover{
    background-color: transparent;
    border:2px solid  tomato;
    color: tomato;
    cursor: pointer;
}

.footer{
    width: 100%;
}
.icons img{
width: 30px;
height: 30px;
margin-right: 50px;
cursor: pointer;
transition: 0.5s;
/* color: tomato; */
}
.icons img:hover{
    transform: translateY(-20px);
}
.icons{
    margin-left: 600px;
    margin-bottom: 30px;
}


.overlap{
    width: 100%;
}
.overlap img{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}
/* .card{
    width: 500px;
} */
.project-container{
    margin-bottom: 100px;
    overflow: hidden;
    padding: 30px;
}
.project-container h1{
    margin-bottom: 100px;
    color: tomato;
    margin-left: 650px;

}
a{
    text-decoration: none;
    color: white;
}
a:hover{
    color: tomato;
}
.card-content{
    width: 260px;
}
.slide img{
    width: 170px;
    height: 170px;
    object-fit: contain;
}
#con{
    margin-right: 100px;
}

.btn-con{
    background-color: green;
    color: white;
    border: green;
    transition: 0.5s;
}
.btn-con:hover{
    border: 2px solid green;
    color: green;
}